<template>
  <div ref="overlay" class="overlay">
    {{ selected.title }}
    <img :src="overlayIcon" alt="" class="overlay-icon" />
  </div>
</template>

<script>
import layerMixin from './layer';

import { state, actions, bus } from '@/components/yeti/yetix';
import ol from '@/js/libs/ol';

let iconSize = 20;

let layerIcon = () => {
  let svg = `
    <svg xmlns="http://www.w3.org/2000/svg" width="${iconSize}" height="${iconSize}" viewBox="0 0 100 100">
      <circle cx="50" cy="50" r="45" fill="#337ab7" stroke="white" stroke-width="10px"/>
      <text x="50" y="70" text-anchor="middle" font-family="sans-serif" font-size="60px" font-weight="bold" fill="white">R</text>
    </svg>`;

  return 'data:image/svg+xml;charset=utf-8,' + encodeURIComponent(svg);
};

let layerStyle = new ol.style.Style({
  image: new ol.style.Icon({
    src: layerIcon(),
    size: [iconSize, iconSize],
  }),
});

let layer = new ol.layer.Vector({
  source: new ol.source.Vector(),
  style: layerStyle,
  name: 'romma',
});
let overlay = new ol.Overlay({
  positioning: 'bottom-center',
  offset: [0, 0],
});

export default {
  mixins: [layerMixin],
  data() {
    return {
      loaded: false,
      selected: {
        title: null,
      },
    };
  },
  computed: {
    showRomma() {
      return state.showRomma;
    },
    overlayIcon() {
      return layerIcon();
    },
  },
  watch: {
    showRomma() {
      if (this.showRomma) {
        if (!this.loaded) {
          actions.fetchRomma().then(this.onLoaded);
          this.loaded = true;
        }
      } else {
        overlay.setPosition(null);
      }
      layer.setVisible(this.showRomma);
    },
  },
  mounted() {
    this.map.addLayer(layer);

    let select = new ol.interaction.Select({
      condition: ol.events.condition.click,
      layers: [layer],
      style: null,
    });
    this.map.addInteraction(select);
    select.on('select', this.onSelectFeature);

    overlay.setElement(this.$refs.overlay);
    this.map.addOverlay(overlay);
  },
  methods: {
    onLoaded(data) {
      let features = this.getFeaturesFromData(data);

      layer.getSource().addFeatures(features);
    },
    onSelectFeature(evt) {
      console.log('click romma', evt);
      let feature = evt.selected.length ? evt.selected[0] : undefined;
      if (this.showRomma) {
        if (feature) {
          this.selected.title = feature.get('name');
          overlay.setPosition(feature.getGeometry().getCoordinates());
        } else {
          overlay.setPosition(null);
        }
      }
    },
  },
};
</script>

<style scoped lang="scss">
.overlay {
  background: #fff;
  border-radius: 4px;
  padding: 20px;
}
.overlay-icon {
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 2;
  pointer-events: none;
}
</style>

<style lang="scss">
/* Not scoped styles */
.yeti-app {
  .layer-overlay {
    background: red;
  }
}
</style>
